@font-face {
  font-family: 'main'; /*a name to be used later*/
  src: url('./Bebas-Regular.ttf'); /*URL to font*/
}

* {
  margin: 0px;
  text-decoration: none;
  color: white;
}

.App {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  background-color:#282c34 !important; 
}

.App-logo {
  position: fixed;
  height: 100%;
  left: -10px;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

a:visited {
  color: white;
}
a {
  text-decoration: none;
  color: white;
}

.info {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  color: white;
  font-family: main;
  font-size: 110px;
  display: inline-block;
}

.meetLink {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 50px;
  padding: 20px;
  background-color: #17181b;
  border-radius: 10px;
  box-shadow: 5px black;
}
.meetLink:hover {
  padding: 21px;
  font-size: 51px;
}

.navBar {
  position: fixed;
  width: 100%;
  height: 8%;
  background-color: #17181b;
  margin: 0;
  padding: 0;
}

ul {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 30%;
  margin: 0;
  list-style-type: none;
  text-align: center;
}

li {
  display:inline;
  padding: 25%;
  font-family: main;
  font-size: 22px;
}

.logo {
  position: absolute;
  height: 70%;
  width: 10%;
  left: 6%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.logo img {
  width: 100%;
  height: 100%;
}